body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  background-color: beige;
  display: flex;
  justify-content: center;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.container {
  width: 500px;
  height: 360px;
  background-color: cornflowerblue;
  border-radius: 8px;
  position: relative;
}

.number-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-flow: row wrap;
  background-color: #E900FF;
  width: 70%;
  height: 65%;
  border-radius: 8px;
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 5px;
}
.number {
  width: 50px;
  height: 52px;
  color: black;
  border-radius: 3px;
  border: 1px solid black;
  font-weight: bold;
  background-color: #FFC600;
  margin: 1px;
  display: flex;
  flex: 1 1 86px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.value-for-showing-content {
  border: 1px solid black;
  border-radius: 3px;
  background-color: white;
  color: black;
  font-weight: bold;
  width: 347px;
  height: 42px;
  position: absolute;
  bottom: 70%;
  margin-left: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#display {
    border: 1px solid black;
    border-radius: 3px;
    background-color: white;
    color: black;
    font-weight: bold;
    width: 347px;
    height: 42px;
    position: absolute;
    bottom: 303px;
    margin-left: 6px;
    display: flex;
    justify-content: center;
    align-items: center;
}

